//SASS VARAIBLES

:root {
  // colors
  --ff--primary: 'Source Sans Pro', sans-serif;
  --ff--secondary: 'Source Code Pro', monospace;
  --fw-reg: 300;
  --fw-bold: 900;
  --clr-light: #fff;
  --clr-dark: #303030;
  --clr-accent: #e6e1e3;
  --clr-import: #e3e4e6;
  --clr-import-blue: #194cda;
  --clr-guestcheck: #3892d5;
  --clr-wik: #c6833c;

  //font-size
  --fs-h1: 3rem;
  --fs-h2: 2.25rem;
  --fs-h3: 1.25rem;
  --fs-body: 1rem;
  --box-shadow:
    0.25em 0.25em 0.75em rgba(0, 0, 0, 0.25),
    0.125em 0.125em 0.25em rgba(0, 0, 0, 0.15);
}

@media (min-width: 800px) {
  :root {
    --fs-h1: 4.5rem;
    --fs-h2: 3.75rem;
    --fs-h3: 1.5rem;
    --fs-body: 1.125rem;
  }
}
